import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message } from 'antd';
import { getPolicyObjects, deletePolicyObject } from 'services/policyObject';

class ListPolicyObjectPage extends Component {
  state = {
    reload: 0,
  };
  handleDelete = async id => {
    await deletePolicyObject(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };
  render() {
    const columns = [
      {
        title: 'Ký hiệu',
        dataIndex: 'id',
        key: 'code',
      },
      {
        title: 'Tên đối tượng',
        dataIndex: 'label',
        key: 'name',
      },
      {
        title: 'Miễn giảm (%)',
        dataIndex: 'decreasePct',
        key: 'maxScores',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#policy-objects/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Đối tượng chính sách">
        <CartHeadAction>
          <BtnAddNew href="#/policy-objects/form/new" />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getPolicyObjects}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListPolicyObjectPage />
  </Page>
);



